.palette {
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.palette-colors {
  height: 90%;
}
.palette-footer {
  background-color: white;
  height: 5vh;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-weight: 600;
}
.emoji {
  font-size: 1.5rem;
  margin: 0 1rem;
}
